import React, {useEffect} from 'react'
import person1 from '../../../tools/img/person1.png'

function KaKaoTalkButton({title, imgUrl, url}) {
    useEffect(() => {
        createKakaoButton()
      }, [])
    
      const createKakaoButton = () => {
        // kakao sdk script   window.Kakao   .
        if (window.Kakao) {
          const kakao = window.Kakao
    
          //  initialization 
          if (!kakao.isInitialized()) {
            //  step   javascript key   initialize
            kakao.init('7ee630bf57da187a1038b1adcf4ef7ec')
          }
    
          kakao.Link.createDefaultButton({
            // Render  id=kakao-link-btn     
            container: '#kakao-link-btn',
            objectType: 'feed',
            content: {
              title: title,
              description: ' ',
              imageUrl: imgUrl, // i.e. process.env.FETCH_URL + '/logo.png'
              // link: {
              //   mobileWebUrl: url,
              //   webUrl: url,
              // },
            },
            // social: {
            //   likeCount: 77,
            //   commentCount: 55,
            //   sharedCount: 333,
            // },
            buttons: [
              {
                title: '  ',
                link: {
                  mobileWebUrl: url,
                  webUrl: url,
                },
              },
            ],
          })
        }
      }

    return (
        <div>
            <button id="kakao-link-btn" style={{backgroundColor:'yellow', padding:'10px'}}>
                 
            </button>
        </div>
    )
}

export default KaKaoTalkButton
